.clearfix {
  zoom: 1;
}
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.product-card {
  zoom: 1;
  position: relative;
  width: 100%;
  margin-bottom: 1.5em;
  overflow: hidden;
}
.product-card:before,
.product-card:after {
  content: "";
  display: table;
}
.product-card:after {
  clear: both;
}
.product-card .wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: column;
  position: relative;
  width: 100%;
}
.product-card .wrap .body {
  padding: 0 1em 1em;
  width: 100%;
  background-color: #ffffff;
  position: relative;
  zoom: 1;
}
.product-card .wrap .body:before,
.product-card .wrap .body:after {
  content: "";
  display: table;
}
.product-card .wrap .body:after {
  clear: both;
}
.product-card .wrap .body .top {
  padding: 1em 0;
  position: relative;
}
.product-card .wrap .body .top .tg {
  position: relative;
  display: inline-block;
  padding: 0.25em 1em;
  background-color: #999999;
  color: #ffffff;
  font-size: 0.8em;
  border-radius: 4em;
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
}
.product-card .wrap .body .top .lbl {
  display: inline-block;
  position: absolute;
  z-index: 2;
  background-color: #f8192c;
  left: auto;
  right: -60px;
  top: -15px;
  height: 60px;
  width: 120px;
  line-height: 90px;
  text-align: center;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.product-card .wrap .body .img {
  zoom: 1;
  position: relative;
  overflow: hidden;
}
.product-card .wrap .body .img:before,
.product-card .wrap .body .img:after {
  content: "";
  display: table;
}
.product-card .wrap .body .img:after {
  clear: both;
}
.product-card .wrap .body .img:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: calc((1/1) * 100%);
  padding-top: -webkit-calc((1/1) * 100%);
}
.product-card .wrap .body .img > .content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.product-card .wrap .body .img img {
  max-width: 100%;
}
.product-card .wrap .body:hover .overlay {
  -webkit-transform: scale(1);
  transform: scale(1);
  -webkit-opacity: 1;
  -moz-opacity: 1;
  opacity: 1;
}
.product-card .wrap .body .overlay {
  position: absolute;
  z-index: 5;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  top: 0;
  left: 0;
  -webkit-transition: all 200ms ease-in-out;
  -moz-transition: all 200ms ease-in-out;
  -ms-transition: all 200ms ease-in-out;
  -o-transition: all 200ms ease-in-out;
  -webkit-opacity: 0;
  -moz-opacity: 0;
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0);
  zoom: 1;
}
.product-card .wrap .body .overlay:before,
.product-card .wrap .body .overlay:after {
  content: "";
  display: table;
}
.product-card .wrap .body .overlay:after {
  clear: both;
}
.product-card .wrap .body .overlay .body-act {
  zoom: 1;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  position: relative;
}
.product-card .wrap .body .overlay .body-act:before,
.product-card .wrap .body .overlay .body-act:after {
  content: "";
  display: table;
}
.product-card .wrap .body .overlay .body-act:after {
  clear: both;
}
.product-card .wrap .body .overlay .body-act .btn {
  margin-bottom: 1em;
  cursor: pointer;
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0.2);
  border-color: #ffffff;
  box-shadow: 0 0 0 transparent;
  border: 1px solid #ffffff;
  padding: 1em 1.5em;
  font-size: 0.85em;
  line-height: 0.85em;
  border-radius: 4em;
}
.product-card .wrap .body .overlay .body-act .btn:focus,
.product-card .wrap .body .overlay .body-act .btn.focus {
  outline: none;
  color: #ffffff;
  background-color: rgba(230, 230, 230, 0.2);
  border-color: #bfbfbf;
}
.product-card .wrap .body .overlay .body-act .btn:hover {
  outline: none;
  color: #ffffff;
  background-color: rgba(230, 230, 230, 0.2);
  border-color: #e0e0e0;
}
.product-card .wrap .body .overlay .body-act .btn:active,
.product-card .wrap .body .overlay .body-act .btn.active,
.open > .dropdown-toggle.product-card .wrap .body .overlay .body-act .btn {
  color: #ffffff;
  background-color: rgba(230, 230, 230, 0.2);
  border-color: #e0e0e0;
}
.product-card .wrap .body .overlay .body-act .btn:active:hover,
.product-card .wrap .body .overlay .body-act .btn.active:hover,
.open > .dropdown-toggle.product-card .wrap .body .overlay .body-act .btn:hover,
.product-card .wrap .body .overlay .body-act .btn:active:focus,
.product-card .wrap .body .overlay .body-act .btn.active:focus,
.open > .dropdown-toggle.product-card .wrap .body .overlay .body-act .btn:focus,
.product-card .wrap .body .overlay .body-act .btn:active.focus,
.product-card .wrap .body .overlay .body-act .btn.active.focus,
.open > .dropdown-toggle.product-card .wrap .body .overlay .body-act .btn.focus {
  color: #ffffff;
  background-color: rgba(212, 212, 212, 0.2);
  border-color: #bfbfbf;
  outline: none;
}
.product-card .wrap .body .overlay .body-act .btn:active,
.product-card .wrap .body .overlay .body-act .btn.active,
.open > .dropdown-toggle.product-card .wrap .body .overlay .body-act .btn {
  background-image: none;
}
.product-card .wrap .body .overlay .body-act .btn.disabled:hover,
.product-card .wrap .body .overlay .body-act .btn[disabled]:hover,
fieldset[disabled] .product-card .wrap .body .overlay .body-act .btn:hover,
.product-card .wrap .body .overlay .body-act .btn.disabled:focus,
.product-card .wrap .body .overlay .body-act .btn[disabled]:focus,
fieldset[disabled] .product-card .wrap .body .overlay .body-act .btn:focus,
.product-card .wrap .body .overlay .body-act .btn.disabled.focus,
.product-card .wrap .body .overlay .body-act .btn[disabled].focus,
fieldset[disabled] .product-card .wrap .body .overlay .body-act .btn.focus {
  background-color: rgba(255, 255, 255, 0.2);
  border-color: #ffffff;
  outline: none;
}
.product-card .wrap .body .overlay .body-act .btn .badge {
  color: rgba(255, 255, 255, 0.2);
  background-color: #ffffff;
}
.product-card .wrap .body .overlay .body-act .btn:last-child {
  margin-bottom: 0;
}
.product-card .wrap .body .card-data {
  position: relative;
  zoom: 1;
  width: 100%;
}
.product-card .wrap .body .card-data:before,
.product-card .wrap .body .card-data:after {
  content: "";
  display: table;
}
.product-card .wrap .body .card-data:after {
  clear: both;
}
.product-card .wrap .body .card-data b {
  display: block;
  color: #000000;
  margin-bottom: 0.5em;
}
.product-card .wrap .body .card-data .colors {
  zoom: 1;
  position: relative;
  margin-bottom: 1em;
}
.product-card .wrap .body .card-data .colors:before,
.product-card .wrap .body .card-data .colors:after {
  content: "";
  display: table;
}
.product-card .wrap .body .card-data .colors:after {
  clear: both;
}
.product-card .wrap .body .card-data .colors > * {
  display: inline-block;
  margin-right: 0.33333333em;
  height: 0.66666667em;
  width: 0.66666667em;
  border-radius: 50%;
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
}
.product-card .wrap .card-ftr {
  zoom: 1;
  position: relative;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  background-color: #000000;
  align-items: center;
}
.product-card .wrap .card-ftr:before,
.product-card .wrap .card-ftr:after {
  content: "";
  display: table;
}
.product-card .wrap .card-ftr:after {
  clear: both;
}
.product-card .wrap .card-ftr > div {
  height: 2.5em;
  line-height: 0.5em;
  padding: 1em;
  color: #ffffff;
}
.product-card .wrap .card-ftr > div:first-child {
  flex: 1 1 50%;
  cursor: pointer;
  text-align: left;
  text-transform: uppercase;
}
.product-card .wrap .card-ftr > div:last-child {
  flex: 1 1 50%;
  text-align: center;
  text-transform: uppercase;
  background-color: #f8192c;
  font-size: 1.35em;
}
